.header {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: $grey;
    width: 100%;
    border-radius: $borderRadius;
    margin: 5px;
    padding: 50px;
    box-shadow: $boxShadow;

    h2 {
        font-size: 3rem;
        background-color: $grey;
    }

    h3 {
        font-size: 2rem;
        background-color: $grey;
    }
   
    .color1:hover {
        font-size: 3rem;
        color:$color1;
        cursor:crosshair;
        transition:0s;
        transform:translate(0, -10px);
    }


    .color2:hover {
        font-size: 3rem;
        color:$color2;
        cursor:crosshair;
        transition:0s;
        transform:translate(0, 9px);
    }

    .color3:hover {
        font-size: 3rem;
        color:$color3;
        cursor:crosshair;
        transition:0s;
        transform:translate(0, -8px);
    }

    .color1, .color2, .color3 {
        position:relative;
        background-color: $grey;
        color:$white;
        transition: 0.5s;
        transition-delay: 0.5s;

    }


    .info {
        font-size: 1.2rem;

        p {
            background-color: $grey;
        }
    }
}
@media screen and (max-width: 700px) {
    .header {
        flex-direction: column;
    }
}