.competencesCont {
    width: 100%;
    margin: 5px;
    background-color: $grey;
    display: flex;
    justify-content: space-evenly;
    border-radius: $borderRadius;
    box-shadow: $boxShadow;

    

    ul{
        display: flex;
        flex-wrap: wrap;
        margin: 5vh;
        font-size: 1.3rem;
        background-color: $grey;
        height: 25vh;
        .entete {
            font-size: 1.1rem;
            display: flex;
            justify-content: center;
            flex-basis: 100%;
        }
    }
    li {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 2vh;
        font-size: 1rem;
        background-color: $grey;
        filter: grayscale(1);
        &:hover {
            filter: grayscale(0);
            .techName, .outilName {
                color: $white;
            }
        }
        img {
            width: 75px;
        }
        .techName, .outilName {
            margin-top: 5px;
            background-color: $grey;
            color: $grey;
        }
    }
}
.experiencesCont, .formationsCont {
    width: 100%;
    margin: 5px;
    background-color: $grey;
    display: flex;
    justify-content: space-evenly;
    border-radius: $borderRadius;
    box-shadow: $boxShadow;

    ul{
        margin: 5vh;
        font-size: 1.3rem;
        background-color: $grey;
    }
    li {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin: 2vh;
        font-size: 1rem;
        background-color: $grey;
        p {
            background-color: $grey;
        }
    }
}