.navigation {
    width: 100%;

    ul {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-evenly;

        li {
            display: flex;
            position:relative;
            align-items: center;
            justify-content: center;
            background-color: $grey;
            border-radius: $borderRadius;
            width: 100%;
            height: 50px;
            margin: 5px;
            cursor: crosshair;
            font-size: 1.3rem;
            box-shadow: $boxShadow;
            transition:0.4s;
        }

        li:hover, .isActive {
            box-shadow: $boxShadowStrong;
            background-image:$gradientLight;
            transform: translate(1px, -1px);
		    transition:0.2s;
        }

        li.experiences:hover {
            background-color:$color1;
        }

        li.competences:hover {
            background-color:$color2;
        }

        li.formations:hover {
            background-color:$color3;
        }

        li.experiences.isActive {
            color:$color1;
        }

        li.competences.isActive {
            color:$color2;
        }

        li.formations.isActive {
            color:$color3;
        }

        li.isActive:hover{
            color:$grey;
        }


    }
}