@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');

$font: 'Josefin Sans', sans-serif;

$grey: #333;
$white: #ddd;

$borderRadius: 25px;

$boxShadow: 3px 3px 5px rgba(0,0,0,0.3);
$boxShadowStrong: 3px 3px 4px 3px rgba(0,0,0,0.5);

$color1: rgb(11, 182, 111);
$color2: rgb(11, 128, 0);
$color3: rgb(7, 70, 165);
$gradientLight: linear-gradient(rgba(100, 100, 100, 0.12), rgba(75, 75, 75, 0.12), rgba(82, 82, 82, 0.137));

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    height:100vh;
    margin: 1rem auto;
    max-width: 1200px;
    font-family: $font;
    color: $white;
    background-image: linear-gradient(130deg, $color2, $color1 41.07%, $color3 76.05%);
}


li {
    list-style-type: none;
}