@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{height:100vh;margin:1rem auto;max-width:1200px;font-family:"Josefin Sans",sans-serif;color:#ddd;background-image:linear-gradient(130deg, #0b8000, #0bb66f 41.07%, #0746a5 76.05%)}li{list-style-type:none}.content{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;background-color:#333;width:100%;border-radius:25px;margin:5px;padding:50px;box-shadow:3px 3px 5px rgba(0,0,0,0.3)}.header h2{font-size:3rem;background-color:#333}.header h3{font-size:2rem;background-color:#333}.header .color1:hover{font-size:3rem;color:#0bb66f;cursor:crosshair;transition:0s;-webkit-transform:translate(0, -10px);transform:translate(0, -10px)}.header .color2:hover{font-size:3rem;color:#0b8000;cursor:crosshair;transition:0s;-webkit-transform:translate(0, 9px);transform:translate(0, 9px)}.header .color3:hover{font-size:3rem;color:#0746a5;cursor:crosshair;transition:0s;-webkit-transform:translate(0, -8px);transform:translate(0, -8px)}.header .color1,.header .color2,.header .color3{position:relative;background-color:#333;color:#ddd;transition:0.5s;transition-delay:0.5s}.header .info{font-size:1.2rem}.header .info p{background-color:#333}@media screen and (max-width: 700px){.header{-webkit-flex-direction:column;flex-direction:column}}.navigation{width:100%}.navigation ul{display:-webkit-flex;display:flex;width:100%;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}.navigation ul li{display:-webkit-flex;display:flex;position:relative;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background-color:#333;border-radius:25px;width:100%;height:50px;margin:5px;cursor:crosshair;font-size:1.3rem;box-shadow:3px 3px 5px rgba(0,0,0,0.3);transition:0.4s}.navigation ul li:hover,.navigation ul .isActive{box-shadow:3px 3px 4px 3px rgba(0,0,0,0.5);background-image:linear-gradient(rgba(100,100,100,0.12), rgba(75,75,75,0.12), rgba(82,82,82,0.137));-webkit-transform:translate(1px, -1px);transform:translate(1px, -1px);transition:0.2s}.navigation ul li.experiences:hover{background-color:#0bb66f}.navigation ul li.competences:hover{background-color:#0b8000}.navigation ul li.formations:hover{background-color:#0746a5}.navigation ul li.experiences.isActive{color:#0bb66f}.navigation ul li.competences.isActive{color:#0b8000}.navigation ul li.formations.isActive{color:#0746a5}.navigation ul li.isActive:hover{color:#333}.competencesCont{width:100%;margin:5px;background-color:#333;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;border-radius:25px;box-shadow:3px 3px 5px rgba(0,0,0,0.3)}.competencesCont ul{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin:5vh;font-size:1.3rem;background-color:#333;height:25vh}.competencesCont ul .entete{font-size:1.1rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-basis:100%;flex-basis:100%}.competencesCont li{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin:2vh;font-size:1rem;background-color:#333;-webkit-filter:grayscale(1);filter:grayscale(1)}.competencesCont li:hover{-webkit-filter:grayscale(0);filter:grayscale(0)}.competencesCont li:hover .techName,.competencesCont li:hover .outilName{color:#ddd}.competencesCont li img{width:75px}.competencesCont li .techName,.competencesCont li .outilName{margin-top:5px;background-color:#333;color:#333}.experiencesCont,.formationsCont{width:100%;margin:5px;background-color:#333;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;border-radius:25px;box-shadow:3px 3px 5px rgba(0,0,0,0.3)}.experiencesCont ul,.formationsCont ul{margin:5vh;font-size:1.3rem;background-color:#333}.experiencesCont li,.formationsCont li{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;margin:2vh;font-size:1rem;background-color:#333}.experiencesCont li p,.formationsCont li p{background-color:#333}

